import React, {useState} from 'react';
import CCheck from "../UI/CCheck";
import {Select} from "antd";
import IconSelect from "../../assets/icons/IconSelect";

const AdditionalFinishingHard = () => {
	const { Option, OptGroup } = Select
	const [form, setForm] = useState({
		cover: '',
		additionalFinishing: '',
		coverLamination: null
	});

	return (
		<div className="cp-row">
			<div className="cp-title"> </div>
			<CCheck value={form.additionalFinishing} onChange={(val) => {
				if (val.includes(' ')) {
					setForm({...form, additionalFinishing: val})
				} else {
					setForm({...form, additionalFinishing: val, coverLamination: null})
				}
			}}>
				<CCheck.Item value=" -"> -</CCheck.Item>
				<CCheck.Item value=" "> </CCheck.Item>
				<CCheck.Item value=" "> </CCheck.Item>
				<CCheck.Item value=""></CCheck.Item>
				<CCheck.Item value=" "> </CCheck.Item>
				<div className={form.additionalFinishing.includes(' ') ? "hidden-line" : "hidden-line hidden" }>
					<Select
						dropdownAlign={{ offset: [0, 10] }}
						placeholder="  *"
						suffixIcon={<IconSelect/>}
						value={form.coverLamination} onChange={(val) => setForm({...form, coverLamination: val})}
					>
						<OptGroup label="  ">
							<Option value=''></Option>
							<Option value=''></Option>
							<Option value='soft touch'>soft touch</Option>
							<Option value=''></Option>
						</OptGroup>
					</Select>
				</div>
			</CCheck>
		</div>
	);
};

export default AdditionalFinishingHard;
